.home-page {
  display: flex;
  flex-direction: column;
  align-items: center;

  .przywitanie {
    background-color: $primary-color;
    color: white;
    text-align: center;
    width: 100%;
    padding: 20px;
    border-radius: $border-radius;
    font-size: 30px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;


    img {
      width: 100px;
      border-radius: 50%;
      margin-bottom: 10px;
      align-items: center;
      
    }
  }


  h1 {
    text-align: center;
    padding: 20px;
    color: $color-h2;
  }

  .news-section {
    padding: 20px;
    margin-bottom: 20px;
    align-items: center;

    .news-list {
      display: flex;
      flex-wrap: wrap;

      .news-item {
        width: calc(33.333% - 20px);
        margin: 10px;
        border-radius: $border-radius;
        align-items: center;
        justify-content: center; 

        img {
          width: auto;
          height: 200px;
          object-fit: cover;
          border-top-left-radius: $border-radius;
          border-top-right-radius: $border-radius;
          align-items: center;
          justify-content: center; 
        }

        .news-content {
          padding: 20px;
          align-items: center;
          justify-content: center; 

          h2 {
            margin-bottom: 10px;
            color: $color-h3;
          }

          p {
            margin-bottom: 20px;
            color: $text-color;
            align-items: center;
            justify-content: center; 
          }
        }
      }
    }
  }
}
